@mixin dropdown-red-bg(){
    background-color: $color-red60;
        color: $color-purewhite;
        padding: $measure--basic $measure--basic 0;
        &:after {
            content: '';
            width: 0;
            border-left: $measure--basic $border-style-solid
                transparent;
            border-right: $measure--basic $border-style-solid
                transparent;
            border-top: $measure--basic $border-style-solid
                $color-red60;
            margin: auto;
            display: block;
            top: $measure--basic;
            position: relative;
        }
        select {
            &:focus {
                outline-color: $color-purewhite;
            }
        }
}